.drop-enter-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.drop-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.drop-enter {
  -webkit-transform: translateY(-300px);
          transform: translateY(-300px);
}
.drop-leave-active {
  -webkit-transform: translateY(-300px);
          transform: translateY(-300px);
}
.dialog-cover {
  background: #000;
  opacity: .3;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dialog-content {
  width: 30%;
  position: fixed;
  max-height: 80%;
  overflow: auto;
  background: #fff;
  top: 20%;
  left: 60%;
  margin-left: -25%;
  z-index: 10;
  border: 2px solid #464068;
  padding: 2%;
  line-height: 1.6;
  text-align: left;
}
.dialog-close {
  float: right;
  position: absolute;
  right: 25px;
  top: 10px;
  font-size: 20pt;
  font-family: "Consolas";
}

